import {CSSProperties} from 'react';
import React from 'react';
import {Button, TextField, Typography} from '@material-ui/core';
import SigninViewModel from '../viewModels/SigninViewModel';
import Summary from '../../../application/components/Summary';


export default class Signin extends SigninViewModel {

    css = {

        form: {
            margin: 0,
            padding: 0,
            display: 'grid',
            alignContent: 'start',
            alignItems: 'center',
            gridTemplateRows: '70% 30%',
            maxWidth: this.isMobile ? '100vw' : '60vw',
        } as CSSProperties,

        input: {
            marginTop: '12px',
            width: '100%',
        } as CSSProperties,
        title: {
            margin: 0,
            fontFamily: 'Comfortaa',
            color: '#0288D1',
        } as CSSProperties,
        subtitle: {
            color: '#03A9F4',
            marginBottom: 12
        } as CSSProperties,
        button: {
            minWidth: '100%',
            margin: '12px 0',
            height: 50,
        } as CSSProperties,

    }


    notify(): React.ReactNode {
        return (
            <form key={'signin'} style={this.css.form} autoComplete='off' aria-disabled={this.state.isLoading}>
                <div>
                    <Typography style={this.css.title} variant='h6' component='div' color='textSecondary'>
                        ACESSAR MINHA CONTA
                    </Typography>
                    <Typography style={this.css.subtitle} variant='button' component='div' color={'textSecondary'}>
                        GENIUS
                    </Typography>
                    
                    <Summary items={this.state.errors} onClose={() => this.clearErrors()}/>

                    <TextField style={this.css.input} autoFocus={true} type='email' name='username' label='usuário'
                               onChange={(e) => this.handleChange(e)}
                               helperText={this.getError('username', 'seu usuário e o e-mail informado no ato do cadastro')}
                               error={this.hasError('username')}/>

                    <TextField style={this.css.input} type='password' name='password' label='Senha'
                               onChange={(e) => this.handleChange(e)}
                               helperText={this.getError('password','informe sua senha')}
                               error={this.hasError('password')}/>
                </div>
                <div>
                    <Button style={this.css.button} variant='contained' color='primary' onClick={() => this.signin()}>
                        ENTRAR AGORA
                    </Button>
                    <Button style={this.css.button} variant='text' color={'primary'}
                            onClick={() => this.props.switch()}>
                        AINDA NÃO SOU CADASTRADO
                    </Button>
                </div>
            </form>
        );
    }
}