import {ViewModel} from '../../../application/ViewModel';
import Users, {SigninModel} from '../models/UserModels';
import Auth from '../../../application/Auth';


export default class SigninViewModel extends ViewModel<SigninModel> {


    async validate() {
        await this.clearErrors();

        if (this.payload.username === '' || this.payload.username === undefined)
            await this.setError('username', 'usuário é um campo obrigatório');


        if (this.payload.password === '' || this.payload.password === undefined)
            await this.setError('password', 'senha é um campo obrigatório');
    }


    async signin() {
        await this.validate();
        
        if (this.hasErrors())
            return;

        if (true)
            this.redirectTo('/admin')
        else
            Users.login(this.payload)
                .then(async (response) => {
                    await Auth.register(response.data.data);
                    await this.redirectTo('/home');
                })
                .catch((error) => {
                    error.response.data.errors.forEach((e: any) => {
                        this.setError(e.property, e.message);
                    });
                });
        
    }

}