import {ViewModel} from '../../../application/ViewModel';
import { LoginModel } from '../models/UserModels';


export default class LoginViewModel extends ViewModel<LoginModel>{
    constructor(props: any) {
        super(props, {initial: {isSigninForm: true}});
    }
    
    switch(){
        this.payload = {...this.payload, isSigninForm: !this.payload.isSigninForm }
    }
}