import {CSSProperties} from 'react';
import React from 'react';
import {
    Card,
    CardContent,
    CardMedia,
    Container,
} from '@material-ui/core';
import LoginViewModel from '../viewModels/LoginViewModel';
import medicos from '../../../assets/img/banner-genius.png';
import UserCreate from './UserCreate';
import Signin from './Signin';


export default class Login extends LoginViewModel {


    css = {
        root: {
            display: 'grid',
            height: '100vh',
        } as CSSProperties,
        card: {
            width: this.isMobile ? '100vw' : '40vw',
            display: 'grid',
            justifySelf: 'center',
            gridTemplateRows: '30vh 70vh',
            overflow: 'hidden auto',
        } as CSSProperties,
        banner: {
            width: '100%',
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'top',
        } as CSSProperties,
        content: {
            display: 'flex',
            justifyContent: 'center',
            maxWidth: '100%'
        } as CSSProperties,
    }


    notify(): React.ReactNode {
        return (
            <div style={this.css.root}>
                <Card style={this.css.card} elevation={0} square={true}>
                    <CardMedia component={'div'} style={this.css.banner} image={medicos} title='medical'/>

                    <CardContent style={this.css.content}>
                        {
                            this.payload.isSigninForm
                                ? <Signin switch={() => this.switch()}/> :
                                <UserCreate switch={() => this.switch()}/>
                        }
                    </CardContent>
                </Card>
            </div>
        );
    }
}